* { 
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

main{
  width: 1140px;
  margin : auto;

  /* min-height : 요소의 최소 높이
          -> 내부 요소가 없어도 최소 높이 유지
          -> 내부 요소가 지정된 크기를 초과하면
              그에 맞에 늘어남
  */
  min-height: 500px;
}

/***************************************************/
/* 게시글 메인화면 */


/* header */
header{
  height: 200px;
  display: flex;
  flex-direction: row;
}

header > section:nth-child(1){ width: 15%;}
header > section:nth-child(2){ width: 70%;}
header > section:nth-child(3){ width: 15%; }


/* 로고 */
header > section:first-child{
  display: flex;
  justify-content: center;
  align-items: center;
  
}

#homeLogo{
  width: 120px;
  height: auto; /* 이미지 기본 비율에 맞게 자동 지정 */
}

/***************************************************/
/* Board Nav */

/* nav */
nav{
  height: 50px;
  position: sticky;
  /* 화면 밖으로 벗어났을 때 
  지정된 위치에 고정*/
  
  top : 0; /* 최상단에 붙임 */
  
  background-color: white;
  border-bottom: 2px solid black;
  z-index: 1000;
}

nav > ul{
  display: flex;
  
  /* li 태그 앞에 기호 삭제 */
  list-style: none;
  height: 100%;
}


nav li{
  flex : 0 1 150px;
  /* grow shrink basis */
}

nav a{
  text-decoration: none;
  color : black;
  font-size: 18px;
  font-weight: bold;
  padding : 11px 0;
  display: block;
  text-align: center;
  
  border-radius: 5px;
  transition-duration: 0.1s;
  height: 100%;
}


nav a:hover{
  color : white;
  background-color: #455ba8;
}


/***************************************************/
/* 메인 - 로그인화면 */

/* content */
.main.content{
  height: 800px;
  display: flex;
  
  /* flex 지정 시 기본값 */
  flex-direction: row;
  justify-content: flex-start;
  /* flex-start : flex요소에만 사용 가능한 정렬 방식 */
  /* start : block요소, flex, grid 등에서도 적용 가능한 정렬 방식 */
  align-items: stretch;
}

.main .content-1{ 
  flex-basis: 70%;
  padding : 20px;
}
.main .content-2{ flex-basis: 30%;}

.main .content-1 > *{
  margin-bottom: 20px;
}

.main .content-1 > pre{
  font-size: 1rem;
  font-family: '고딕';
}

/* 로그인 영역 */
.main .content-2 > div{
  height: 150px;
  padding : 10px;
  margin-top : 10px;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main .id-pw-area{
  border: 1px solid #ccc;
  display: flex;

  /* 중심축 방향 크기 지정 */
  flex-basis: 60%;
}

/* id/pw 입력 영역*/
.main .id-pw-area > section:first-child{
  width: 75%;
  display: flex;
  flex-direction: column;
}

.main .id-pw-area > section:first-child > input{
  border : 0;
  border-right : 1px solid #ccc;
  flex-basis: 50%;
  outline: none;
  padding: 5px;
}

/* id input 태그 */
.main input[name='inputId']{
  border-bottom : 1px solid #ccc !important;
}

/* id/pw에 초점이 맞춰졌을 때 */
.main .id-pw-area > section:first-child > input:focus{
  border: 2px solid #455ba8 !important;
}


/* 로그인 버튼 영역 */
.main .id-pw-area > section:last-child{
  width: 25%;
  display: flex;
  justify-content: center;
}

/* 로그인 버튼 */
.main .id-pw-area > section:last-child > button{
  width: 100%;
  background-color: white;
  cursor: pointer;
  border : none;
}

/* 로그인 버튼에 마우스가 올라갔을 때 */
.main .id-pw-area > section:last-child > button:hover{
  background-color: #455ba8;
  color: white;
}




.board-area{
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  /* flex-direction: column; */

  height: 90px;

  padding: 30px 40px;
  margin: 10px 0;

  border-bottom: 1.5px solid #ddd;
}

.board-title{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  
  width: 800px;
  height: 30px;

  color: black;
  font-weight: bold;
  font-size: 1.0em;
}

.board-title > a{
  display: flex;
  justify-content: center;
  align-items: center;
}

.board-title > a > :nth-child(1){
  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
}

.like{
  display: flex;
  justify-content: center;
  align-items: center;

  color: #455ba8;
  font-size: 1.3em;

  width: 30px;

  cursor: pointer;
}
.like-count{
  display: flex;
  justify-content: center;
  align-items: center;

  width: 15px;
}

.board-info{
  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: 500px;
  height: 30px;
}

.modalBtn-area{
  display: flex;
  justify-content: center;
  align-items: center;

  width: 300px;
  height: 60px;
}

.modalBtn-area > button{
  display: flex;
  justify-content: center;
  align-items: center;

  width: 80px;
  height: 25px;

  border: 2px solid #455ba8;
  color: #455ba8;
  font-weight: bold;
  background-color: white;

  cursor: pointer;

  margin-left: 80px;
}

.modal{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  width: 1140px;
  height: 250px;

  padding: 15px;

  font-size: 0.9em;

  border: 3px solid #455ba8;
  border-radius: 10px;

  background-color: #eee;
}

.modal-content > :nth-child(1){
  font-weight: bold;
  font-size: 1.3em;
  margin-bottom: 10px;
}
.modal-content > :nth-child(4){
  font-size: 1.1em;
  margin-top: 10px;
}
.modal-content > :nth-child(5){
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  width: 60px;
  height: 30px;

  background-color: #455ba8;
  border: 2px solid white;
  border-radius: 10px;
  color: white;

  font-weight: bold;

  margin-top: 100px;

  cursor: pointer;
}

*::selection {
  background-color: transparent; /* 선택된 영역의 배경색을 투명하게 설정 */
  color: inherit; /* 선택된 텍스트의 색상을 부모 요소의 색상으로 설정 */
}





/* 게시판 삽입 */
.insert-area{
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 500px;
  height: 50px;

  margin: auto 0;
  margin-top: 25px;
}
.board-insert-1 > :nth-child(1){
  display: flex;
  justify-content: center;
  align-items: center;

  width: 300px;
  height: 30px;

  border: 1.5px solid #455ba8;
}
.board-insert-2 > :nth-child(1) , .board-insert-3 > :nth-child(1){
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  width: 75px;
  height: 30px;

  background-color: #455ba8;
  color: white;
  font-weight: bold;

  border: 1.5px solid black;
  cursor: pointer;

  margin: 0 12px;
}
.board-insert-3 > :nth-child(1){
  margin-left: -0px;
}
.insertBoard{
  display: flex;
  justify-content: start;
  align-items: center;
  text-align: center;

  margin-top: 40px;
  margin-left: 7px;
}
.insertBoard > :nth-child(1){
  border: 2px solid #455ba8;
  color: #455ba8;
  cursor: pointer;
  font-weight: bold;
  font-size: 1.1em;
  text-decoration: none;  
}
a{
  cursor: pointer;
}



.error-page{
  display: flex;
  height: 800px;
  justify-content: center;
  align-items: center;
  text-align: center;

  color: red;
  font-size: 2.0em;
  font-weight: bold;
}







/* 게시글 상세 영역 board detail */
.detail-area{

  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;

  padding: 10px;
  margin-top: 40px;

  width: 1140px;
  height: 850px;

  border: 3px solid #455ba8;
  border-radius: 15px;
}

.detail-title{
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 1100px;
  height: 100px;

  font-size: 2.0em;
  font-weight: bold;
}

.detail-info{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  width: 1100px;
  height: 100px;

  padding: 10px 5px;

  border-top: 3px solid #455ba8;
  border-bottom: 3px solid #455ba8;
}

.detail-info > :nth-child(1){
  display: flex;
  justify-content: start;
  align-items: center;

  width: 850px;
  height: 100px;

  font-size: 1.2em;
  font-weight: bold;
}
.detail-info > :nth-child(2){
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  font-weight: bold;

  width: 250px;
  height: 100px;
}
.detail-info-like{
  font-size: 1.5em;
  color: #455ba8;
  margin: 0 5px;
}

.detail-content{
  display: flex;
  justify-content: start;
  align-items: start;

  width: 1100px;
  height: 600px;

  padding: 10px;
  padding-top: 20px;
}

.detail-button{
  display: flex;
  justify-content: end;
  align-items: center;

  width: 1100px;
  height: 50px;
}

.detail-button > :nth-child(n){
  display: flex;
  justify-content: center;
  align-items: center;

  width: 80px;
  height: 30px;

  background-color: #455ba8;
  border: 0;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
}
.detail-button > :nth-child(2){
  margin: 0 15px;
}



/* 게시글 수정 CSS */
.editedTitle{
  width: 1100px;
  height: 100px;

  border-radius: 5px;
  border: 3px solid #455ba8;

  font-weight: bold;
  font-size: 1.5em;

  padding-left: 10px;
}

.editedContent{
  width: 1100px;
  height: 600px;

  border-radius: 5px;
  border: 3px solid #455ba8;

  font-size: 1.2em;

  padding: 10px;
  margin-top: 20px;

  resize: none;
}

.editedBtn{
  width: 100px;
  height: 35px;

  border-radius: 7px;
  border: 3px solid #455ba8;
  background-color: white;

  font-weight: bold;
  font-size: 1.1em;
  color: #455ba8;

  margin-top: 20px;

  cursor: pointer;
}
.editedBtn:hover{
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.5);
}

.editedBtnArea{
  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 300px;
}
